    padding: 20px;
    overflow: hidden;
    border-bottom: 1px solid #666;
    .image {
        width: 300px;
        height: 200px;
        float: left;
        margin-right: 10px;
        overflow: hidden;
        a {
            display: block;
            width: 300px;
            height: 200px;
            img {
                width: 100%;
                line-height: 200px;
                text-align: center;
            }
        }
    }
    .words {
        max-height: 200px;
        overflow: hidden;
        .title {
            height: 30px;
            line-height: 30px;
            a {
                color: rgb(10, 129, 240);
