<template>
    <div class="banner-container" @mouseenter="autoStop" @mouseleave="autoStart">
        <ul
            class="images"
            :style="{
                width: banners.length * 100 + '%',
                marginLeft: -index * 100 + '%',
            }"
        >
            <li v-for="(item, i) in banners" :key="i">
                <a :href="item.link"><img :src="item.url" alt="" /></a>
            </li>
        </ul>
        <ul class="dots">
            <li
                v-for="(item, i) in banners"
                :key="i"
                :class="{
                    active: i === index,
                }"
                @click="index = i"
            ></li>
        </ul>
    </div>
</template>



<script>
//js
export default {
    props: {
        banners: {
            type: Array, //属性类型是数组
            required: true, //必须要传递改属性
        },
        duration:{
            type: Number,
            default: 2000, //属性默认值
        }
    },
    created() {
        this.autoStart();
    },
    unmounted() {
        this.autoStop();
    },
    data() {
        return {
            index: 1, //表示显示的是第几张图片
            timer: null,
        };
    },
    methods: {
        //自动开始
        autoStart() {
            if (this.timer) {
                return;
            }
            this.timer = setInterval(() => {
                    this.index = (this.index + 1) % this.banners.length;
            }, this.duration);
        },
        //停止自动切换
        autoStop() {
            clearInterval(this.timer);
            this.timer = null;
        },
    },
};
</script>



<style lang="less" scoped>
/* 样式 */
.banner-container {
    position: relative;
    width: 1190px;
    margin: 0 auto;
    height: 500px;
    overflow: hidden;
    padding-top: 20px;
    ul.images {
        height: 500px;
        transition: all 1s ease;
        li {
            float: left;
            width: 1190px;
            height: 500px;
            img {
                width: 100%;
                height: 100%;
            }
        }
    }
    ul.dots {
        width: 300px;
        position: absolute;
        bottom: 20px;
        display: flex;
        justify-content: center;
        left: 50%;
        margin-left: -150px;
        li {
            display: block;
            width: 12px;
            height: 12px;
            cursor: pointer;
            margin: 0 10px;
            border-radius: 50%;
            color: #fff;
            border: 1px solid #fff;
            background-color: #666;
            &.active {
                background-color: #fff;
            }
        }
    }
}
</style>