<template>
    <div class="news-types" v-if="channels.length > 0">
        <div
            class="item"
            v-for="item in showChannels"
            :key="item.channelId"
            @click="switchTo(item.channelId)"
            :class="{
                active: item.channelId === chooseId,
            }"
        >
            <span class="name">{{ item.name }}</span>
        </div>
        <a href="" @click.prevent="isCollapse = !isCollapse">{{
            isCollapse ? "展开" : "收起"
        }}</a>
    </div>
</template>




<script>
import { getNewsChannels } from "@/services/newsService";
export default {
    data() {
        return {
            channels: [],
            isCollapse: true, //当前是否为折叠状态
            chooseId: null, //当前选中的频道id
        };
    },
    computed: {
        showChannels() {
            if (this.isCollapse) {
                //折叠状态
                return this.channels.slice(0, 8); //折叠状态显示9个
            } else {
                return this.channels;
            }
        },
    },
    async created() {
        var resp = await getNewsChannels();
        this.channels = resp;
        //此时才有频道
        this.switchTo(this.channels[0].channelId);
    },
    methods: {
        //切换chooseId
        switchTo(id) {
            this.chooseId = id;
            //触发一个事件，让组件的使用者去搞定这件事
            this.$emit('change', this.chooseId);
        },
    },
};
</script>








<style lang="less" scoped>
.news-types {
    margin: 30px 0;
    padding: 10px;
    border-bottom: 1px solid #ccc;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    font-size: 14px;
    padding-bottom: 15px;
    .item {
        padding: 10px 20px;
        border-radius: 6px;
        height: 20px;
        line-height: 20px;
        border: 1px solid #ccc;
        margin: 7px 15px 7px 0;
        cursor: pointer;
        position: relative;
        &:hover {
            background-color: #ccc;
        }
        &:active {
            color: #fff;
            background-color: #000;
        }
    }
    .active{
        color: #fff;
        background-color: #000;
    }
    a {
        align-self: flex-end;
        margin: 20px 20px 20px 0px;
        color: rgb(6, 115, 240);
    }
}
</style>