<template>
    <div>
        <Banner :banners="banners" :duration="3000"/>
        <Channels @change="handleChange"/>
        <Loading v-if="isLoading" />
        <NewsList  v-show="!isLoading" :news="news"/>
        
    </div>
</template>

<script>
import Banner from "../components/Banner";
import Channels from "../components/news/Channels";
import NewsList from "../components/news/NewsList";
import Loading from "../components/Loading";
import { getNews } from "../services/newsService";
export default {
    components: {
        Banner,
        Channels,
        NewsList,
        Loading,
    },
    data() {
        return {
            banners: [
                {
                    url: require("../assets/banners/demo1.jpg"),
                    link: "http://www.melikui.com",
                },
                {
                    url: require("../assets/banners/demo2.jpg"),
                    link: "http://www.melikui.com",
                },
                {
                    url: require("../assets/banners/demo3.jpg"),
                    link: "http://www.melikui.com",
                },
                {
                    url: require("../assets/banners/demo4.jpg"),
                    link: "http://www.melikui.com",
                },
            ],
            news: [],
            isLoading: true,
        };
    },
    methods: {
        async handleChange(channelId) {
            this.isLoading = true;
            var resp = await getNews(channelId, 1, 10);
            this.news = resp.contentlist;
            this.isLoading = false;
        },
    },
};
</script>

